@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: -webkit-gradient(linear, left top, right top, from(#0e0e0e), to(#151616));
  background-image: -webkit-linear-gradient(left, #0e0e0e, #151616);
  background-image: linear-gradient(to right, #0e0e0e, #151616);
  /* background-image: linear-gradient(to right, #fdfffc, #ebebeb); */
}

.App {
  margin-left: 5rem;
  margin-right: 5rem;
}

.navbarContainer {
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 9999;
}

.navbar {
  position: fixed;
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background-image: linear-gradient(to right, #1a1a1a, #00092a); */
  background-image: -webkit-gradient(linear, left top, right top, from(#0e0e0e), to(#151616));
  background-image: -webkit-linear-gradient(left, #0e0e0e, #151616);
  background-image: linear-gradient(to right, #0e0e0e, #151616);
  /* background-image: linear-gradient(to right, #fdfffc, #ebebeb); */
}

.logo {
  font-family: "Bebas Neue", sans-serif;
  font-size: 50px;
  margin-left: 100px;
  /* color: #bababa; */
  background: -webkit-linear-gradient(30deg, #e26d5c, #ffe1a8);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  /* color: #383d3b; */
}

.navcontent {
  font-family: "Bebas Neue", sans-serif;
  display: flex;
  justify-content: space-between;
  margin-right: 100px;
  width: 30%;
  font-size: 30px;
  color: #bababa;
}

.navcontent a {
  padding-left: 20px;
  color: #bababa;
  /* color: #383d3b; */
  text-decoration: none;
}

.content {
  height: 500px;
  width: 100%;
  display: flex;
  justify-content: center;
}

.mainParagraph {
  margin-top: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.mainParagraph p {
  margin-top: 200px;
  width: 50%;
